import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import houseType from 'components/__types__/house';
import HouseFieldTable from 'components/house-field-table/HouseFieldTable';

const HouseDetails = ({ t, house }) => {
  return (
    <Box>
      <h3 data-testid="details_title">
        {t('common.widgetName', {
          widgetNamePlaceholder: 'House',
        })}
      </h3>
      <HouseFieldTable house={house} />
    </Box>
  );
};

HouseDetails.propTypes = {
  house: houseType,
  t: PropTypes.func.isRequired,
};

HouseDetails.defaultProps = {
  house: {},
};

export default withTranslation()(HouseDetails);
